@import url('https://fonts.googleapis.com/css2?family=Jaro:opsz@6..72&family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');

.container{
    display: flex;
    flex-direction: row;
    text-align: left;

    /*Variable depending on width*/
    padding: 5px;

    width: 100%;
    background-color: black;
    border-radius: 5px;
    justify-content:stretch;
    font-family: "Roboto Mono", monospace;


}

.track_info{
    display: flex;
    width: 90%;
    padding: 0;
    margin: 0;
    flex-direction: column;
    justify-content: start;
    
}

.track_action{
    display: flex;
    width: 10%;
    padding: 0;
    margin: 0;
    align-items: center;
    justify-content: center;
}

.container h3{
    color:greenyellow;

}

.container p{
    font-style: italic;
    color: white;

}


.button{
    color: lightgreen;
    background-color: black;
    border-style: hidden;
    padding: 0;
    margin: 0;

    /*Variable depending on width*/
    font-size: 2rem;
    align-self: center;
}