@import url('https://fonts.googleapis.com/css2?family=Jaro:opsz@6..72&display=swap');

.button{

    /*Variable depending on width*/
    margin: 5px;

    /*Variable depending on width*/
    padding: 5px 10px;

    align-self: center;
    justify-self: center;

    /*Variable depending on width*/
    font-size: 1.5rem;
    
    background-color: black;
    color: yellowgreen;
    font-weight: bold;
    border-radius: 15px;
    font-family: "Jaro", sans-serif;
}

@media only screen and (min-width: 1500px){

    .button{

        /*Variable depending on width*/
        margin: 10px ;
    
        /*Variable depending on width*/
        padding: 5px;

        /*Variable depending on width*/
        font-size: 2rem;
        
    }

}