@import url('https://fonts.googleapis.com/css2?family=Jaro:opsz@6..72&display=swap');

.App {
  height: 100%;
  width: 100%;
  margin: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  
  /*Variable depending on width*/
  margin-top: 50px;

  padding: 0 25px;
  
}

h1{
  /*Variable depending on width*/
  font-size: 2.5rem;
  line-height: 2rem;
  font-family: "Jaro", sans-serif;
  min-width: 300px;
}

#form-container{
  min-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center; 
  padding: 10px;

  
}

#main-div{
  display: flex;
  flex-direction: column-reverse;
  width: 100%;

  /*Variable depending on width*/
  min-height: 70%;

  /*Variable depending on width*/
  padding: 5px;

  /*Variable depending on width*/
  gap: 5px;
}


#results-div, #playlist-div{
  min-height: 100%;

  /*Variable depending on width*/
  width: 100%;


}


@media only screen and (min-width: 1500px){
  .App {
    flex-direction: column;
  
    /*Variable depending on width*/
    margin-top: 300px;
  }

  h1{
    /*Variable depending on width*/
    font-size: 4rem;
  }

  #main-div{
    display: flex;
    flex-direction: row;
    justify-content: center;
  
    /*Variable depending on width*/
    min-height: 600px;
  
    /*Variable depending on width*/
    padding: 25px;
  
    /*Variable depending on width*/
    gap: 25px;
  }


  #results-div, #playlist-div{
    /*Variable depending on width*/
    width: 40%;

  }

}